@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;600;700&display=swap);
:root {
    --orange: #f9c132;
    --blue-dark: #174a83;
    --blue-light: #4cbad4;
    --gray-bg: #f8f8f8;
    --gray-text: #b2b1b6;
    --temp-bg: #476685;
}

body {
    margin: 0;
    font-family: "Roboto", sans-serif;
}

/* page layouts */

.wrapper {
    max-width: 1100px;
    margin: 0 auto;
    padding: 3rem 0;
}

.header-section {
    /* background: var(--temp-bg) url(../images/header-bg.jpeg); */
    color: white;
    background-image: linear-gradient(#2866acaf, #1d5596a9), url(../../static/media/header-bg.362d170b.jpeg);
    background-color: #174a83;
}

.header-section__title {
    font-size: 3.75rem;
    max-width: 800px;
    margin: 1rem 0;
    font-weight: 600;
    letter-spacing: 1px;
    padding-left: 3rem;
    margin-top: 2rem;
}

.header-section__desc {
    font-size: 1.3rem;
    line-height: 1.5;
    max-width: 565px;
    font-weight: 300;
    margin-bottom: 2rem;
    padding-left: 3rem;
}

.header-section__btn {
    margin-left: 3rem;
    margin-bottom: 3rem;
}

@media (max-width: 768px) {
    .header-section {
        padding-left: 2rem;
        padding-right: 2rem;
    }
    .header-section__title {
        padding-left: 0;
        max-width: inherit;
    }
    .header-section__desc {
        padding-left: 0;
        max-width: inherit;
    }
    .header-section__btn {
        margin-left: 0;
        margin-bottom: 0;
    }
}

@media (max-width: 576px) {
    .header-section {
        padding-left: 1rem;
        padding-right: 1rem;
        text-align: center;
    }
    .header-section__title {
        font-size: 2.75rem;
    }
}

.btn {
    font-weight: 700;
    text-transform: uppercase;
    border: none;
    color: white;
    background: gray;
    padding: 10px 20px;
    font-size: 1rem;
}
.btn i {
    margin-right: 10px;
}
.btn--orange {
    background: var(--orange);
}
.btn--blue {
    background: var(--blue-light);
    margin: 0;
}

.features-section {
    background: var(--gray-bg);
}
.features-section__title {
    text-align: center;
    color: var(--blue-dark);
    font-size: 2.5rem;
    font-weight: 500;
    margin: 1rem 0;
}
.features-section__card-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.card {
    text-align: center;
    max-width: 300px;
    padding: 10px;
}
.card__icon {
    color: var(--blue-light);
    font-size: 1.5rem;
    margin: 10px 0;
}
.card__title {
    font-weight: 600;
    font-size: 1.25rem;
    color: var(--blue-dark);
    margin: 0 0 2rem;
    position: relative;
}
.card__title::after {
    content: "";
    display: block;
    height: 4px;
    width: 30px;
    background: var(--blue-light);
    position: absolute;
    top: 2.25rem;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 25px;
}
.card__desc {
    color: var(--gray-text);
    line-height: 1.5;
    padding: 0 6px;
}

.business-section__title {
    font-size: 2.5rem;
    text-align: center;
    font-weight: 700;
    color: var(--blue-dark);
    margin: 1rem 0;
}
.business-section__nav {
    margin: 0 2rem;
    display: flex;
}
.business-section__nav-link {
    text-decoration: none;
    padding: 10px 20px;
    font-weight: 500;
    color: var(--blue-dark);
}
.business-section__nav-link--active {
    color: var(--blue-light);
    background: var(--gray-bg);
}
.business-section__card {
    display: flex;
    background: var(--gray-bg);
    margin: 0 2rem;
    padding: 1.5rem;
}
.business-section__image {
    flex: 1 1;
    background: url(../../static/media/business-seciton-1.3165794c.jpeg) no-repeat center center;
    background-size: contain;
}
.business-section__content {
    flex: 1 1;
    padding: 0 1rem;
}
.business-section__content-title {
    color: var(--blue-dark);
    font-weight: 600;
    font-size: 1.7rem;
    line-height: 1.35;
}
.business-section__content-desc {
    color: var(--gray-text);
    font-size: 1rem;
    margin: 1rem 0;
    line-height: 1.5;
}

@media (max-width: 768px) {
    .business-section__nav {
        justify-content: center;
        flex-wrap: wrap;
    }
    .business-section__nav-link {
        text-align: center;
    }
    .business-section__card {
        flex-direction: column;
    }
    .business-section__image {
        min-height: 300px;
        background-size: cover;
        margin-bottom: 1rem;
    }
}

@media (max-width: 576px) {
    .business-section__card {
        padding: 1rem;
    }
    .business-section__content {
        text-align: center;
        padding: 0;
    }
    .business-section__nav-link {
        flex: 1 1;
    }
}

.choose-card-section {
    background: var(--gray-bg);
}
.choose-card__container {
    margin: 0 2rem;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.choose-card__section-one {
    flex: 1.5 1;
    margin: 1rem;
}
.choose-card__section-two {
    flex: 1 1;
    margin: 1rem;
    display: flex;
    flex-direction: column;
}

.choose-card__section-one__title {
    font-size: 2.25rem;
    font-weight: 600;
    letter-spacing: 2px;
    color: var(--blue-dark);
    margin: 4rem 0 10px 0;
    padding-right: 2rem;
}
.choose-card__section-one__desc {
    color: var(--gray-text);
    font-size: 0.9rem;
    line-height: 1.5;
    padding-right: 4rem;
}
.choose-card__section-two__image {
    height: 75%;
}

.choose-card__section-two__image-one {
    background: url(data:image/jpeg;base64,) no-repeat center center;
    background-size: cover;
}
.choose-card__section-two__image-two {
    background: url(../../static/media/choose-card-2.ac3e4a60.jpeg) no-repeat center center;
    background-size: cover;
}
.choose-card__section-two__text {
    margin: 1.5rem 0 0 0;
    font-weight: 600;
    color: var(--blue-dark);
}

@media (max-width: 768px) {
    .choose-card__container {
        text-align: center;
    }
    .choose-card__section-one__title {
        margin-top: 1.5rem;
        padding-right: 0;
    }
    .choose-card__section-one__desc {
        padding-right: 0;
    }
    .choose-card__section-two__image {
        min-height: 250px;
        background-size: contain;
        height: 70%;
    }
    .choose-card__section-two__text {
        margin: 5px 0;
    }
}
@media (max-width: 576px) {
    .choose-card__container {
        flex-direction: column;
    }
    .choose-card__section-two,
    .choose-card__section-one {
        margin: 0;
    }
    .choose-card__section-one__title {
        letter-spacing: none;
    }
    .choose-card__section-two__text {
        margin: 10px 0;
    }
}



